body{
  font-family: Dongle;
}
#Header{
  width:auto;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bolder;
  font-size: 80px;
  font-family: Dongle;
  background-color: rgb(255, 246, 234);
}
#Button{
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width:30px;
  height:30px;
  position:absolute;
  left: 2%;
  top: 2.5%;
  background-color: antiquewhite;
  border-radius: 10%;
}
#Button:hover{
  color: grey;
}
#Quit{
  display: flex;
  align-items: center;
  justify-content: center;
  width:30px;
  height: 30px;
  background-color: rgb(255, 246, 234);
  font-size: 30px;
  font-weight: bolder;
  position:absolute;
  cursor: pointer;
  right: 1.5%;
  top:9%;
  border-radius: 10%;
}
#Quit:hover{
  color: grey;
}
#Time{
  width:auto;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 40px;
  font-family: Dongle;
  background-color: rgb(255, 246, 234);
}
#Bottom{
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  width:auto;
  height: 70px;
  background-color: rgb(255, 246, 234);
}
.Container{
  display: flex;
  justify-content: center;
  position: relative;
  width:20%;
  height:0;
  padding-bottom: 14%;
  margin: 10px;
  margin-bottom: 4%;
}
.Box{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  width:auto;
  height:auto;
  background-color: antiquewhite;
  padding-top: 20px;
  padding-bottom: 20px;
}
.Cal{
  position: absolute;
	width: 100%;
	height:100%;
  display: flex;
  justify-content:baseline;
  align-items: center;
  flex-wrap: wrap;
}
.Date{
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  width:14.2857142857%;
  height:15%;
  background-color: rgb(255, 246, 234);
  font-size: 30px;
}
.Date:hover{
  font-size:40px;
  background-color: rgb(253, 238, 219);
  font-weight:900;
  color:grey;
}
.Month{

  position:fixed;
  left:69%;
  color:rgb(248, 115, 115);
}

#Explain{
  width:auto;
  height:800px;
  background-color: antiquewhite;
  font-size: 30px;
  padding: 30px;
}

.Color{
  float: left;
  margin: 12px;
  width:53px;
  height:53px;
  line-height: 500%;
  text-align: center;
}
.Color1{
background-color: #feccdd;
}
.Color2{
background-color: #f9b;
}
.Color3{
background-color: #f69;
}
.Color4{
background-color: #f37;
}
.Github{
  float: right;
  position:relative;
  width:40px;
  height:40px;
  top:40px;
  line-height: 100%;
}
.Github:hover{
  opacity: 0.5;
}
.Insta{
  float: right;
  position:relative;
  width:40px;
  height:40px;
  top:40px;
  line-height: 100%;
}
.Insta:hover{
  opacity: 0.5;
}
.Velog{
  float: right;
  position:relative;
  width:40px;
  height:40px;
  top:40px;
  line-height: 100%;
}
.Velog:hover{
  opacity: 0.5;
}
.Color img{
  width:100%;
  height:100%;
  border-radius: 20%;
}